.delivery {
  padding: 100px 0;
  background: #fff;
}
.container {
}
.visually-hidden {
}
.delivery__list {
  display: flex;
  justify-content: center;
  gap: 39px;
}
.delivery__item {
  width: 250px;
  position: relative;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.03);
  padding-top: 101px;
}

.delivery__item::before {
  position: absolute;
  content: '';
  width: 51px;
  height: 43px;
  background-image: url(../images/delivery2.svg);
  background-repeat: no-repeat;
  background-position: center;
  left: 50%;
  top: 33px;
  transform: translateX(-50%);
}

.delivery__title {
  font-weight: 600;
  font-size: 20px;
  line-height: 1.4;
  color: #000000;
  text-align: center;
  margin-bottom: 9px;
}
.delivery__text {
  font-size: 14px;
  line-height: 1.35;
  text-align: center;
  color: rgba(0, 0, 0, 0.6);
  padding-left: 26px;
  padding-right: 26px;
  padding-bottom: 27px;
}
